.ButtonContainer {
    width: auto;
    display: flex;
    justify-content: center;
    
}
.createTodoButton {
    background-color: #2d839b;
    box-shadow: 0px 5px 15px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    z-index: 1;
    transform: rotate(0);
    transition: .3s ease;
    bottom:48px;
    position: fixed;
  }
  
  .createTodoButton:hover {
    transform: rotate(224deg);
  }